import React from "react";
import axios from "axios";
import CovidCard from "../../Components/CovidCard/CovidCard";
export default class extends React.Component {
  state = {
    data: []
  };
  componentDidMount() {
    axios({
      method: "GET",
      url:
        "https://coronavirus-monitor.p.rapidapi.com/coronavirus/cases_by_particular_country.php",
      headers: {
        "content-type": "application/octet-stream",
        "x-rapidapi-host": "coronavirus-monitor.p.rapidapi.com",
        "x-rapidapi-key": "6031df45bfmshc5765d0ea25d076p1365efjsne5a49e4220bd"
      },
      params: {
        country: "Pakistan"
      }
    }).then(response => {
      let data = [...response.data.stat_by_country].reverse().map(item => {
        item.record_date = item.record_date.split(" ")[0];
        return item;
      });
      let unique = [];
      for (let i = 0; i < data.length; i++) {
        let found = false;
        for (let j = 0; j < unique.length; j++) {
          if (data[i].record_date === unique[j].record_date) {
            found = true;
            break;
          }
        }
        if (!found) {
          unique.push(data[i]);
        }
      }
      this.setState({ data: unique });
    });
  }
  render() {
    return (
      <React.Fragment>
        {this.state.data.map((item, key) => (
          <CovidCard {...item} key={key} />
        ))}
      </React.Fragment>
    );
  }
}
