import React from "react";
import Card from "@material-ui/core/Card";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";

const strToInt = str => {
  return typeof str === "string" ? parseInt(str.replace(/,/g, "")) : str;
};

export default props => (
  <Card variant="outlined" key={props.country_name}>
    <CardContent>
      <Typography color="textSecondary" gutterBottom>
        {props.record_date}
      </Typography>
      <Typography variant="h5" component="h2">
        Cases: {props.active_cases}
      </Typography>
      {strToInt(props.new_cases) ? (
        <Typography color="textSecondary" variant="h5" component="h2">
          New cases: {props.new_cases},{" "}
          {(
            (strToInt(props.new_cases) / strToInt(props.active_cases)) *
            100
          ).toFixed(2) + "%"}
        </Typography>
      ) : null}
      {strToInt(props.total_deaths) ? (
        <Typography>
          Deaths: {props.total_deaths},{" "}
          {(
            (strToInt(props.total_deaths) / strToInt(props.active_cases)) *
            100
          ).toFixed(2) + "%"}
        </Typography>
      ) : null}
      {strToInt(props.new_deaths) ? (
        <Typography color="textSecondary" variant="h5" component="h2">
          New deaths: {props.new_deaths},{" "}
          {(
            (strToInt(props.new_deaths) / strToInt(props.total_deaths)) *
            100
          ).toFixed(2) + "%"}
        </Typography>
      ) : null}
      {strToInt(props.serious_critical) ? (
        <Typography variant="h5" component="h2">
          Critical: {props.serious_critical},{" "}
          {(
            (strToInt(props.serious_critical) / strToInt(props.active_cases)) *
            100
          ).toFixed(2) + "%"}
        </Typography>
      ) : null}
      {strToInt(props.total_recovered) ? (
        <Typography variant="h5" component="h2">
          Recovered: {props.total_recovered},{" "}
          {(
            (strToInt(props.total_recovered) / strToInt(props.active_cases)) *
            100
          ).toFixed(2) + "%"}
        </Typography>
      ) : null}
      {strToInt(props.total_cases_per1m) ? (
        <Typography color="textSecondary" variant="h5" component="h2">
          Cases per million: {props.total_cases_per1m} <br />
        </Typography>
      ) : null}
    </CardContent>
  </Card>
);
